import React, { useState, useEffect } from 'react';
import { useSwipeable } from 'react-swipeable';
import LoadingScreen from './components/LoadingScreen';
import * as Constants from './assets/constsloaders';
import './App.css';

const App = () => {
  const [currentTab, setCurrentTab] = useState(0);
  const [loading, setLoading] = useState(true);
  const [contentToDisplay, setContentToDisplay] = useState([[], [], []]);
  const [currentContentIndex, setCurrentContentIndex] = useState(0);
  const [direction, setDirection] = useState(null); // Track swipe direction

  useEffect(() => {
    const fetchAssets = async () => {
      try {
        const fetchedData = [
          [Constants.menuPage7,Constants.menuPage3, Constants.menuPage1],
          [Constants.menuPage3, Constants.menuPage5,Constants.menuPage1],
          [Constants.menuPage6,Constants.menuPage2, Constants.menuPage7],
        ];

        setTimeout(() => {
          setContentToDisplay(fetchedData);
          setLoading(false);
        }, 2000);
      } catch (error) {
        console.error('Error fetching assets:', error);
        setLoading(false);
      }
    };

    fetchAssets();
  }, []);

  useEffect(() => {
    const handleOrientation = () => {
      if (window.innerHeight < window.innerWidth) {
        alert('Please rotate your device to portrait mode.');
      }
    };
  
    window.addEventListener('resize', handleOrientation);
    handleOrientation(); // Initial check
  
    return () => window.removeEventListener('resize', handleOrientation);
  }, []);



  const handleTabSwitch = (newTabIndex) => {
    if (newTabIndex >= 0 && newTabIndex < contentToDisplay.length) {
      setDirection(newTabIndex > currentTab ? 'right' : 'left');
      setCurrentTab(newTabIndex);
      setCurrentContentIndex(0); // Reset content index when tab changes
    }
  };

  const handleContentSwitch = (newContentIndex) => {
    const currentTabContent = contentToDisplay[currentTab];
    if (newContentIndex >= 0 && newContentIndex < currentTabContent.length) {
      setDirection(newContentIndex > currentContentIndex ? 'down' : 'up');
      setCurrentContentIndex(newContentIndex);
    }
  };

  // Swipe handlers
  const handlers = useSwipeable({
    onSwipedLeft: () => handleTabSwitch(currentTab + 1),
    onSwipedRight: () => handleTabSwitch(currentTab - 1),
    onSwipedUp: () => handleContentSwitch(currentContentIndex + 1),
    onSwipedDown: () => handleContentSwitch(currentContentIndex - 1),
  });

  if (loading) {
    return <LoadingScreen />;
  }

  const currentContent = contentToDisplay[currentTab][currentContentIndex];
  const isVideo = currentContent.endsWith('.mp4');
  const contentClassName = `video-container ${direction}`;

  return (
    <div {...handlers} className="app">
      <div className={contentClassName}>
        {isVideo ? (
          <video src={currentContent} loop autoPlay type="video/mp4" />
        ) : (
          <img src={currentContent} alt="img" />
        )}
      </div>
      <div className="tab-menu">
        {['Menu', 'Specials', 'Extras'].map((tabName, index) => (
          <div
            key={index}
            className={`tab ${currentTab === index ? 'active' : ''}`}
            onClick={() => handleTabSwitch(index)}
          >
            {tabName}
          </div>
        ))}
      </div>
    </div>
  );
};

export default App;
